<template>
  <v-card>
    <v-card-title>
      <span
        >Deassign sites of <span class="red--text light">{{ user.username }}</span></span
      >
    </v-card-title>
    <v-card-text class="pa-0">
      <v-container>
        <v-text-field
          v-model="search"
          @input="onSearchChange"
          class="pa-5"
          append-icon="fa-search"
          label="Search selected sites"
          single-line
          v-if="userSelectedSites.length > 0"
          hide-details
        ></v-text-field>
        <v-data-table
          show-select
          :headers="headers"
          v-if="userSelectedSites.length > 0"
          :search="search"
          :custom-filter="filterCriteria"
          v-model="selectedItems"
          item-key="uuid"
          :items-per-page="10"
          :hide-default-footer="userSelectedSites.length < 10"
          :items="userSelectedSites"
        >
        </v-data-table>
        <v-alert v-else class="mb-0 text-left" dense type="info">
          No sites are assigned to {{ user.username }}
        </v-alert>
      </v-container>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn
        x-small
        class="mr-1 btn-height-22 white--text"
        color="blue darken-1"
        :loading="isSubmitting"
        :disabled="!selectedItems.length"
        @click="onSubmit"
      >
        Deassign
      </v-btn>
      <v-btn x-small class="mr-1 btn-height-22" text @click="onClose">Cancel</v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
export default {
  name: 'map-user-site-form',
  props: ['sites', 'user', 'onClose', 'handleSubmit', 'sites-map'],
  data() {
    return {
      valid: false,
      isSubmitting: false,
      search: '',
      searchItems: [],
      formErrorMessage: '',
      selectedSites: [],
      selectedItems: [],
      headers: [
        {
          text: 'Site Name',
          align: 'start',
          color: 'primary',
          sortable: true,
          value: 'name',
        },
        {
          text: 'City',
          align: 'center',
          sortable: true,
          value: 'city',
        },
        {
          text: 'Country',
          align: 'center',
          sortable: true,
          value: 'country',
        },
        {
          text: 'Timezone',
          align: 'right',
          sortable: true,
          value: 'timezone',
          default: '-',
        },
      ],
    };
  },
  computed: {
    userSelectedSites() {
      const userSites = this.user.sites.map(
        item =>
          (this.sitesMap &&
            this.sitesMap[item.site_uuid] && { ...this.sitesMap[item.site_uuid], userSiteUUID: item.uuid }) ||
          null,
      );
      return userSites;
    },
  },

  methods: {
    onSubmit() {
      if (typeof this.handleSubmit === 'function') {
        this.isSubmitting = true;
        this.handleSubmit(
          this.user,
          this.selectedItems.map(item => ({ site_uuid: item.uuid, user_site_mapping_uuid: item.userSiteUUID })),
        )
          .catch(err => {})
          .finally(() => {
            this.isSubmitting = false;
          });
      }
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },
  },
};
</script>
