<template>
  <div>
    <page-title title="User Management">
      <template v-slot:titleActions>
        <tooltip-icon-button
          btnColor="blue"
          btnIcon="fa-cogs"
          btnTooltip="Configure user's push notification"
          :onClick="() => openFCMDialog()"
        ></tooltip-icon-button>
      </template>
    </page-title>
    <v-tabs v-model="tab" right :color="`${$vuetify.theme.dark ? '': 'nubeGreen'}`">
      <v-tab :key="0">User Sites </v-tab>
      <v-tab :key="1">App Users </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="0">
        <user-site-grid></user-site-grid>
      </v-tab-item>
      <v-tab-item :key="1">
        <user-management-grid v-if="tab == 1"></user-management-grid>
      </v-tab-item>
    </v-tabs-items>
    <v-dialog v-model="configFCMDialog" presistent max-width="350">
      <configure-fcm-key :close-dialog="closeFCMDialog"></configure-fcm-key>
    </v-dialog>
  </div>
</template>
<script>
import PageTitle from '../Common/PageTitle.vue';
import UserManagementGrid from './userManagementGrid.vue';
import UserSiteGrid from '../UserSite/UserSiteGrid.vue';
import ConfigureFcmKey from './configureFcmKey.vue';
import TooltipIconButton from '../RubixApps/TooltipIconButton.vue';
export default {
  name: 'user-management',
  components: { PageTitle, UserManagementGrid, TooltipIconButton, ConfigureFcmKey, UserSiteGrid },
  watch: {
    '$route.query.tab': function (val) {
      this.tab = this.tabs.indexOf(val);
    },
    'tab': function (val, oldVal) {
      const { tab } = this.$route.query;
      if (tab !== this.tabs[val]) {
        const tabKey = this.tabs[val];
        this.$router.push({ path: 'user-management', query: { tab: tabKey } });
      }
    },
  },
  data() {
    return { configFCMDialog: false, tab: 0, tabs: ['user-sites', 'app-users'] };
  },
  methods: {
    openFCMDialog() {
      this.configFCMDialog = true;
    },
    closeFCMDialog() {
      this.configFCMDialog = false;
    },
  },
};
</script>
