<template>
  <div>
    <v-toolbar flat>
      <v-toolbar-title class="mr-6">User Sites</v-toolbar-title>
      <v-btn x-small @click="toggleDialog(dialogNames.siteDialog)" class="mr-1 btn-height-22 white--text" color="pink">
        New Site<v-icon x-small right> fa-plus </v-icon>
      </v-btn>
      <v-text-field
        v-model="search"
        append-icon="fa-search"
        label="Search site"
        single-line
        @input="onSearchChange"
        hide-details
        class="pl-10"
      ></v-text-field>
      <v-spacer></v-spacer>
    </v-toolbar>
    <v-data-table
      v-model="selectedItems"
      item-key="uuid"
      show-select
      :loading="isFetchingSites"
      :search="search"
      :custom-filter="filterCriteria"
      :headers="headers"
      :items="items"
      class="elevation-1"
      :footer-props="tableProps.FOOTER_PROPS"
      :itemsPerPage="tableProps.ITEMS_PER_PAGE"
    >
      <template v-slot:[`item.actions`]="{ item }">
        <tooltip-icon-button
          btnColor="blue darken"
          btnIcon="fa-pencil"
          btnTooltip="Edit Site"
          :onClick="() => toggleDialog(dialogNames.editSiteDialog, item)"
        ></tooltip-icon-button>
        <tooltip-icon-button
          btnColor="error darken"
          btnIcon="fa-trash-o"
          btnTooltip="Delete Site"
          :onClick="() => toggleDialog(dialogNames.removeSite, item)"
        ></tooltip-icon-button>
      </template>
    </v-data-table>
    <v-dialog v-model="dialog[dialogNames.siteDialog]" persistent max-width="500px">
      <user-site-form
        v-if="dialog[dialogNames.siteDialog]"
        :handle-save="createSite"
        :on-close="() => closeDialog(dialogNames.siteDialog)"
      ></user-site-form>
    </v-dialog>
    <v-dialog v-model="dialog[dialogNames.editSiteDialog]" persistent max-width="500px">
      <user-site-form
        v-if="dialog[dialogNames.editSiteDialog]"
        :on-close="() => closeDialog(dialogNames.editSiteDialog)"
        :site="selectedApp"
        :is-edit-form="true"
        :handle-save="editSite"
      ></user-site-form>
    </v-dialog>
    <confirm-dialog
      :is-open="dialog[dialogNames.removeSite]"
      :on-close="() => closeDialog(dialogNames.removeSite)"
      :on-confirm="() => removeSite()"
      :loading="isDeletingSite"
      dialog-text="Do you want to delete the User Site?"
    ></confirm-dialog>
  </div>
</template>

<script>
import UserSiteForm from './UserSiteForm.vue';
import GridToolbar from '../Common/GridToolbar';
import ConfirmDialog from '../Common/ConfirmDialog.vue';
import { DATA_TABLE_PROPS, USER_VERIFICATION_STATE } from '../../constants/appConstant';
import * as userSitesService from '../../services/userSitesService';

import TooltipIconButton from '../RubixApps/TooltipIconButton.vue';
const dialogNames = {
  siteDialog: 'siteDialog',
  removeSite: 'remove',
  editSiteDialog: 'editSiteDialog',
};

export default {
  name: 'user-site-grid',
  components: { GridToolbar, TooltipIconButton, ConfirmDialog, UserSiteForm },
  data: () => ({
    search: '',
    searchItems: [],
    site: {},
    siteDialog: false,
    isDeletingSite: false,
    items: [],
    dialog: false,
    selectedApp: {},
    dialogNames: { ...dialogNames },
    dialog: {
      [dialogNames.siteDialog]: false,
      [dialogNames.removeSite]: false,
      [dialogNames.editSiteDialog]: false,
    },
    isFetchingSites: false,
    selectedItems: [],
    headers: [
      {
        text: 'Site name',
        align: 'start',
        sortable: true,
        value: 'name',
      },
      { text: 'Address', align: 'center', value: 'address', sortable: true },
      { text: 'City', align: 'center', value: 'city', sortable: true },
      { text: 'State', align: 'center', value: 'state', sortable: true },
      { text: 'Zip Code', align: 'center', value: 'zip', sortable: true },
      { text: 'Latitude', align: 'center', value: 'lat', sortable: true },
      { text: 'Longitude', align: 'center', value: 'lon', sortable: true },
      { text: 'Timezone', align: 'center', value: 'time_zone', sortable: true },
      { text: 'Action', align: 'right', value: 'actions' },
    ],
    tableProps: { ...DATA_TABLE_PROPS },
  }),
  computed: {
    dataItems() {
      return (this.items || [])
        .filter(item => this.filters.isUnverified && !item.state === 'UNVERIFIED')
        .map(item => ({
          ...item,
          isEditing: false,
        }));
    },
  },
  mounted() {
    this.initialize();
  },
  methods: {
    initialize() {
      this.fetchSites();
    },
    toggleDialog(dialogName, item) {
      this.dialog[dialogName] = !this.dialog[dialogName];

      if (this.dialog[dialogName]) {
        this.selectedApp = item;
      } else {
        this.selectedApp = null;
      }
    },
    removeSite() {
      this.isDeletingSite = true;
      return userSitesService
        .deleteSite(this.selectedApp.uuid)
        .then(() => {
          this.$toasted.show('Successfully deleted user site address.');
          this.closeDialog(this.dialogNames.removeSite);
          this.fetchSites();
        })
        .finally(() => {
          this.isDeletingSite = false;
        });
    },
    closeDialog(dialogName, syncAppDetails) {
      this.dialog[dialogName] = false;
      this.selectedApp = {};
    },
    createSite(siteAddress) {
      // do not catch error here
      return userSitesService.createSite(siteAddress).then(() => {
        this.$toasted.show('Successfully created user site address.');
        this.closeDialog(this.dialogNames.siteDialog);
        this.fetchSites();
      });
    },
    editSite(siteAddress, uuid) {
      return userSitesService.updateSite(uuid, siteAddress).then(() => {
        this.$toasted.show('Successfully updated user site address.');
        this.closeDialog(this.dialogNames.editSiteDialog);
        this.fetchSites();
      });
    },
    fetchSites() {
      this.isFetchingSites = true;
      return userSitesService
        .fetchSites()
        .then(data => {
          this.items = data;
        })
        .catch(err => {})
        .finally(() => {
          this.isFetchingSites = false;
        });
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },
  },
};
</script>
