v<template>
  <div>
    <v-toolbar flat>
      <v-toolbar-title class="mr-6">Users</v-toolbar-title>
      <v-btn
        @click="() => verifyUsers(selectedItems)"
        :disabled="selectedItems.length == 0"
        x-small
        color="nubeGreen"
        class="mr-3 btn-height-22 white--text"
      >
        Verify User
        <v-icon x-small right dark> fa-check </v-icon>
      </v-btn>
      <v-btn
        @click="() => toggleDialog(dialogNames.deleteUsers)"
        :disabled="selectedItems.length == 0"
        x-small
        color="red darken-1"
        class="mr-3 btn-height-22 white--text"
      >
        Delete User(s)
        <v-icon x-small right dark> fa-trash-o </v-icon>
      </v-btn>
      <!-- <v-btn x-small class="white--text" color="pink" dark> New User <v-icon x-small right dark> fa-plus </v-icon></v-btn> -->

      <v-text-field
        v-model="search"
        @input="onSearchChange"
        append-icon="fa-search"
        label="Search"
        single-line
        hide-details
        class="pl-10"
      ></v-text-field>
      <v-menu bottom left>
        <template v-slot:activator="{ on, attrs }">
          <v-btn icon v-bind="attrs" v-on="on" :color="appliedFilterCount > 0 ? 'blue' : ''">
            <v-icon>fa-filter</v-icon>
          </v-btn>
        </template>

        <v-list dense>
          <v-subheader>Filters</v-subheader>

          <v-list-item>
            <v-list-item-title>
              <v-checkbox
                class="mt-0"
                hide-details
                v-model="filters.isUnverified"
                label="Unverified Users"
              ></v-checkbox>
            </v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
      <v-spacer></v-spacer>
    </v-toolbar>
    <v-data-table
      show-select
      item-key="uuid"
      :search="search"
      :headers="headers"
      :items="dataItems"
      class="elevation-1"
      v-model="selectedItems"
      :loading="isFetchingUsers"
      :custom-filter="filterCriteria"
      :footer-props="tableProps.FOOTER_PROPS"
      :itemsPerPage="tableProps.ITEMS_PER_PAGE"
    >
      <template v-slot:[`item.actions`]="{ item }">
        <div>
          <tooltip-icon-button
            btnIcon="fa-map"
            btnColor="nubeGreen"
            btnTooltip="Assign site"
            buttonSize="medium"
            :onClick="() => toggleDialog(dialogNames.assignSite, item)"
            :loading="item.isVerifying"
          ></tooltip-icon-button>

          <tooltip-icon-button
            btnIcon="fa-map-o"
            btnTooltip="Deassign site"
            btnColor="red darken-1"
            buttonSize="medium"
            :onClick="() => toggleDialog(dialogNames.deassignSite, item)"
            :loading="item.isVerifying"
          ></tooltip-icon-button>
        </div>
      </template>
      <template v-slot:[`item.state`]="{ item }">
        <span class="font-weight-medium">
          <v-chip v-if="item.state === 'VERIFIED'" small dark color="darken-2 blue">VERIFIED</v-chip>
          <v-chip v-if="item.state === 'UNVERIFIED'" small dark color="darken-1 brown">UNVERIFIED</v-chip>
        </span>
      </template>
      <template v-slot:[`item.assignedSites`]="{ item }">
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <p class="mb-0 text-ellipses text-center" v-bind="attrs" v-on="on">
              <span>{{ item.userSite }}</span>
            </p>
          </template>
          <span>{{ item.userSite }}</span>
        </v-tooltip>
      </template>
    </v-data-table>
    <v-dialog v-model="dialog[dialogNames.deassignSite]" persistent max-width="500px">
      <deassign-site-form
        v-if="dialog[dialogNames.deassignSite]"
        :sites="userSites"
        :sites-map="userSitesMap"
        :user="selectedItem"
        :handleSubmit="deassignSite"
        :onClose="() => closeDialog(dialogNames.deassignSite)"
      ></deassign-site-form>
    </v-dialog>
    <v-dialog v-model="dialog[dialogNames.assignSite]" persistent max-width="500px">
      <map-user-site-form
        v-if="dialog[dialogNames.assignSite]"
        :sites="userSites"
        :user="selectedItem"
        :handleSubmit="assignSite"
        :onClose="() => closeDialog(dialogNames.assignSite)"
      ></map-user-site-form>
    </v-dialog>
    <confirm-dialog></confirm-dialog>
    <confirm-dialog
      :is-open="dialog[dialogNames.deleteUsers]"
      :on-close="() => closeDialog(dialogNames.deleteUsers)"
      :on-confirm="deleteUsers"
      :loading="isDeletingUsers"
      dialog-text="Are you sure to delete User(s)?"
    ></confirm-dialog>
  </div>
</template>

<script>
import GridToolbar from '../Common/GridToolbar';
import ConfirmDialog from '../Common/ConfirmDialog.vue';
import { DATA_TABLE_PROPS, USER_VERIFICATION_STATE } from '../../constants/appConstant';
import * as userManagementService from '../../services/userManagementService';
import * as userSitesService from '../../services/userSitesService';
import MapUserSiteForm from './mapUserSiteForm.vue';
import DeassignSiteForm from './deassignSiteForm.vue';
import TooltipIconButton from '../RubixApps/TooltipIconButton.vue';
const dialogNames = {
  assignSite: 'assignSite',
  deassignSite: 'deassignSite',
  deleteUsers: 'deleteUsers',
};
export default {
  name: 'discover-devices',
  components: { GridToolbar, MapUserSiteForm, DeassignSiteForm, ConfirmDialog, TooltipIconButton },
  data: () => ({
    search: '',
    searchItems: [],
    userSites: [],
    isDeletingUsers: false,
    userSitesMap: null,
    selectedItem: {},
    items: [],
    dialog: false,
    isFetchingUsers: false,
    selectedItems: [],
    isVerifying: false,
    filters: {
      isUnverified: false,
    },
    dialogNames: { ...dialogNames },
    dialog: {
      [dialogNames.assignSite]: false,
      [dialogNames.deassignSite]: false,
      [dialogNames.deleteUsers]: false,
    },
    userVerificationState: { ...USER_VERIFICATION_STATE },
    headers: [
      {
        text: 'First Name',
        align: 'start',
        sortable: true,
        value: 'first_name',
      },
      { text: 'Last Name', align: 'center', value: 'last_name', sortable: true },
      { text: 'Username', align: 'center', value: 'username', sortable: true },
      { text: 'Email', align: 'center', value: 'email', sortable: true },
      { text: 'Verification Status', align: 'center', value: 'state', sortable: true },
      {
        text: 'Assigned Sites',
        align: 'center',
        value: 'assignedSites',
        width: '20ch',
      },
      { text: 'Action', align: 'center', value: 'actions' },
    ],
    tableProps: { ...DATA_TABLE_PROPS },
  }),
  computed: {
    appliedFilterCount() {
      return Object.keys(this.filters).reduce((acc, filterKey) => {
        if (this.filters[filterKey]) {
          acc++;
        }
        return acc;
      }, 0);
    },
    dataItems() {
      this.userSitesMap; // trigger the view re-rendering
      return (this.items || [])
        .map(item => ({
          ...item,
          isVerifying: false,
          userSite: this.getSiteNamesForUser(item),
          assignedSitesNumber: (item.sites || []).length,
        }))
        .filter(val => !(this.filters.isUnverified && val.state !== USER_VERIFICATION_STATE.UNVERIFIED));
    },
  },
  mounted() {
    this.initialize();
  },
  methods: {
    getSiteNamesForUser(user) {
      const userSiteMapping = user.sites || [];
      if (!userSiteMapping.length) {
        return 'Not assigned';
      }

      if (!this.userSitesMap) {
        return 'Loading...';
      }

      if (this.userSitesMap) {
        const userSites = userSiteMapping
          .map(siteMapping => this.userSitesMap[siteMapping.site_uuid])
          .filter(val => val);
        return userSites.map(userSite => userSite.name).join(', ');
      }
    },
    async initialize() {
      this.isFetchingUsers = true;
      await this.fetchUsers();
      await this.fetchUserSites();
      this.isFetchingUsers = false;
    },
    fetchUserSites() {
      return userSitesService.fetchSites().then(data => {
        this.userSites = data;
        this.userSitesMap = this.userSites.reduce((acc, userSite) => {
          acc[userSite.uuid] = { ...userSite };

          return acc;
        }, {});
      });
    },
    toggleDialog(dialogName, item) {
      this.dialog[dialogName] = !this.dialog[dialogName];

      if (this.dialog[dialogName]) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
      this.selectedItem = {};
    },
    fetchUsers() {
      return userManagementService
        .fetchUsers()
        .then(data => {
          this.items = data;
        })
        .catch(err => {});
    },
    verifyUsers() {
      const unverifiedUsers = this.selectedItems.filter(item => item.state == USER_VERIFICATION_STATE.UNVERIFIED);
      if (!unverifiedUsers.length) {
        this.selectedItems = [];
        return this.$toasted.show('Please select unverified users!', { type: 'info' });
      }
      this.isVerifying = true;
      return userManagementService
        .verifyUsers(unverifiedUsers)
        .then(() => {
          this.$toasted.show(`Successfully verified #${unverifiedUsers.length} number of user(s).`);
          this.fetchUsers();
        })
        .finally(() => {
          this.selectedItems = [];
        });
    },
    verifyUser(item) {
      item.isVerifying = true;
      return userManagementService
        .verifyUser({ username: item.username })
        .then(() => {
          this.$toasted.show(`${item.username} verified`);
          this.selectedItems = [];

          this.fetchUsers();
        })
        .catch(err => {})
        .finally(() => {
          item.isVerifying = false;
        });
    },
    assignSite(user, selectedSites) {
      const userId = user.uuid;
      return userSitesService.createSitesMapping(userId, selectedSites).then(() => {
        this.$toasted.show(`Successfully assigned #${selectedSites.length} number of sites to user: ${user.username}`);
        this.closeDialog(this.dialogNames.assignSite);
        this.fetchUsers();
      });
    },
    deassignSite(user, selectedSites) {
      const userId = user.uuid;
      return userSitesService.deleteSitesMapping(userId, selectedSites).then(() => {
        this.$toasted.show(`Successfully deassigned sites of user: ${user.username}`);
        this.closeDialog(this.dialogNames.deassignSite);
        this.fetchUsers();
      });
    },
    deleteUsers() {
      const userIds = this.selectedItems.map(user => user.uuid);
      this.isDeletingUsers = true;
      return userManagementService
        .deleteUsers(userIds)
        .then(() => {
          this.$toasted.show(`Successfully deleted # ${userIds.length} number of users.`);
          this.closeDialog(this.dialogNames.deleteUsers);
          this.selectedItems = [];
          this.fetchUsers();
        })
        .catch(err => {})
        .finally(() => {
          this.isDeletingUsers = false;
        });
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },
  },
};
</script>
<style scoped>
.text-ellipses {
  max-width: 20ch;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>
