import http from '../axios';

const endpoint = api => `/user-management${api ? '' + api : ''}`;

const usersEndpoint = uuid => `${endpoint('/users')}${uuid ? '/' + uuid : ''}`;

export const fetchFCMServerKey = () => {
  return http.get(endpoint('/fcm'));
};

export const updateFCMServerKey = fcmKey => {
  return http.put(endpoint('/fcm'), { key: fcmKey });
};

export const fetchUsers = () => {
  return http.get(usersEndpoint());
};

export const fetchUserById = uuid => {
  return http.get(usersEndpoint(uuid));
};

export const updateUserById = (uuid, body) => {
  return http.put(usersEndpoint(uuid), body);
};

export const deleteUser = uuid => {
  return http.delete(usersEndpoint(uuid));
};

export const deleteUsers = uuids => {
  return Promise.all(uuids.map(uuid => deleteUser(uuid)));
};

export const verifyUser = user => {
  return http.post(endpoint('/verify-user'), user);
};

export const verifyUsers = users => {
  return Promise.all(users.map(user => verifyUser(user).catch(err => err)));
};
