<template>
  <v-card :loading="isFetchingFCMKey">
    <v-card-title><span>Configure Push Notification</span></v-card-title>
    <v-card-text>
      <v-form v-model="valid" ref="form" lazy-validation>
        <v-row>
          <v-col cols="12" class="mt-1">
            <p class="mb-0 text-left" v-if="placeholderKey">
              <span class="caption">Previous Key:</span>
              <span class="white--text body-1">{{ placeholderKey }}</span>
            </p>
            <v-text-field
              :disabled="isFetchingFCMKey"
              v-model="fcmKey"
              label="FCM Key"
              required
              :rules="[rules.required]"
            ></v-text-field>
          </v-col> </v-row
      ></v-form>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="darken-1" text @click="closeDialog"> Cancel </v-btn>
      <v-btn color="blue darken-1" text @click="updateFCMKey()"> Save </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import * as userManagementService from '../../services/userManagementService';
export default {
  name: 'configure-fcm-key',
  props: ['closeDialog'],
  data() {
    return {
      fcmKey: '',
      placeholderKey: '',
      valid: false,
      isFetchingFCMKey: false,
      isUpdatingFCMKey: false,
      rules: { required: value => !!value || 'Required.' },
    };
  },
  mounted() {
    this.intialize();
  },
  methods: {
    intialize() {
      this.fetchFCMKey();
    },
    fetchFCMKey() {
      this.isFetchingFCMKey = true;
      return userManagementService
        .fetchFCMServerKey()
        .then(data => {
          this.placeholderKey = data.key;
        })
        .catch(err => {})
        .finally(() => {
          this.isFetchingFCMKey = false;
        });
    },
    updateFCMKey() {
      if (!this.$refs.form.validate()) {
        return;
      }
      this.isUpdatingFCMKey = true;
      return userManagementService
        .updateFCMServerKey(this.fcmKey)
        .then(() => {
          this.fetchFCMKey();
          this.$refs.form.reset();
          this.closeDialog();
        })
        .catch(err => {
          if (err && err.message) {
            this.$toasted.show(err.message, { type: 'error' });
          }
        });
    },
  },
};
</script>
