<template>
  <v-card class="map-user-site-form">
    <v-card-title>
      <span
        >Assign Site to <span class="nubeGreen--text">{{ user.username }}</span></span
      >
    </v-card-title>
    <v-card-text class="pa-0">
      <v-container>
        <v-text-field
          v-model="search"
          @input="onSearchChange"
          class="pa-5"
          append-icon="fa-search"
          label="Search selected sites"
          single-line
          v-if="siteItems.length > 0"
          hide-details
        ></v-text-field>
        <v-data-table
          show-select
          :headers="headers"
          v-if="siteItems.length > 0"
          :search="search"
          v-model="selectedSites"
          :custom-filter="filterCriteria"
          item-key="uuid"
          :items-per-page="10"
          :hide-default-footer="siteItems.length < 10"
          :items="siteItems"
        >
        </v-data-table>
        <v-alert class="mb-0 text-left" v-if="siteItems.length === 0" dense type="info">
          All location sites have been assigned to {{ user.username }}
        </v-alert>
      </v-container>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn
        x-small
        class="mr-1 btn-height-22 white--text"
        :loading="isSubmitting"
        color="nubeGreen"
        :disabled="!selectedSites.length"
        @click="onSubmit"
      >
        Assign
      </v-btn>
      <v-btn x-small class="mr-1 btn-height-22" text @click="onClose"> Cancel </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
export default {
  name: 'map-user-site-form',
  props: ['sites', 'user', 'onClose', 'handleSubmit'],
  data() {
    return {
      valid: false,
      isSubmitting: false,
      search: '',
      searchItems: [],
      formErrorMessage: '',
      selectedSites: [],
      headers: [
        {
          text: 'Site Name',
          align: 'start',
          color: 'primary',
          sortable: true,
          value: 'name',
        },
        {
          text: 'City',
          align: 'center',
          sortable: true,
          value: 'city',
        },
        {
          text: 'Country',
          align: 'center',
          sortable: true,
          value: 'country',
        },
        {
          text: 'Timezone',
          align: 'right',
          sortable: true,
          value: 'timezone',
          default: '-',
        },
      ],
    };
  },
  computed: {
    siteItems() {
      const userSites = this.user.sites.map(item => item.site_uuid);
      const sites = this.sites.filter(site => !~userSites.indexOf(site.uuid));
      return sites;
    },
  },
  methods: {
    onSubmit() {
      if (typeof this.handleSubmit === 'function') {
        this.isSubmitting = true;
        this.handleSubmit(
          this.user,
          this.selectedSites.map(item => item.uuid),
        )
          .catch(err => {})
          .finally(() => {
            this.isSubmitting = false;
          });
      }
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },
  },
};
</script>
<style scoped>
.map-user-site-form .v-list-item__title {
  text-align: left !important;
}
</style>
