import http from '../axios';

const endpoint = uuid => `/user-sites${uuid ? '/' + uuid : ''}`;

export const fetchSites = () => {
  return http.get(endpoint());
};

export const createSite = site => {
  return http.post(endpoint(), site);
};

export const updateSite = (uuid, site) => {
  return http.patch(endpoint(uuid), site);
};

export const deleteSite = uuid => {
  return http.delete(endpoint(uuid));
};

export const fetchBySiteId = uuid => {
  return http.get(endpoint(uuid));
};

export const createSiteMapping = site => {
  return http.post(`${endpoint()}/mapping`, site);
};

export const deleteSiteMapping = site => {
  return http.post(`${endpoint()}/remove-mapping`, site);
};

export const createSitesMapping = (userId, siteIds) => {
  return Promise.all(siteIds.map(siteId => createSiteMapping({ user_uuid: userId, site_uuid: siteId })));
};

export const deleteSitesMapping = (userId, sites) => {
  return Promise.all(sites.map(site => deleteSiteMapping({ user_uuid: userId, site_uuid: site.site_uuid, user_site_mapping_uuid: site.user_site_mapping_uuid })));
};
