<template>
  <v-card>
    <v-card-title>Site Form</v-card-title>
    <v-card-text>
      <v-container>
        <v-form v-model="valid" ref="form" lazy-validation>
          <v-row>
            <v-col cols="12" sm="6" md="6">
              <v-text-field
                dense
                label="Site Name"
                v-model="formData.name"
                required
                :rules="[rules.required]"
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="6">
              <v-text-field
                dense
                label="Street Address"
                v-model="formData.address"
                required
                :rules="[rules.required]"
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="6">
              <v-text-field
                dense
                label="City/Town"
                v-model="formData.city"
                required
                :rules="[rules.required]"
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="6">
              <v-text-field
                dense
                label="State"
                v-model="formData.state"
                required
                :rules="[rules.required]"
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="6">
              <v-text-field
                dense
                label="Zip/Postcode"
                v-model="formData.zip"
                required
                :rules="[rules.required]"
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="6">
              <v-text-field
                dense
                label="Country"
                v-model="formData.country"
                required
                :rules="[rules.required]"
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="6">
              <v-text-field
                dense
                label="Latitude"
                v-model="formData.lat"
                required
                :rules="[rules.required]"
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="6">
              <v-text-field
                dense
                label="Longitude"
                v-model="formData.lon"
                required
                :rules="[rules.required]"
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="12" md="12">
              <v-autocomplete
                dense
                v-model="formData.time_zone"
                required
                label="Timezone"
                :rules="[rules.required]"
                :items="timezones"
                clearable
              ></v-autocomplete>
            </v-col>
          </v-row>
        </v-form>
        <v-alert v-if="!!formErrorMessage" class="text-left" dense outlined type="error">
          {{ formErrorMessage }}
        </v-alert>
      </v-container>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn
        x-small
        class="mr-1 btn-height-22 white--text"
        color="blue darken-1"
        :loading="isSubmitting"
        :disabled="!valid"
        @click="onSubmit"
        >Save</v-btn
      >
      <v-btn x-small class="mr-1 btn-height-22" text @click="onClose">Cancel</v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import momentTz from 'moment-timezone/builds/moment-timezone-with-data.js';
export default {
  props: ['site', 'isEditForm', 'onClose', 'handleSave'],
  data() {
    return {
      valid: false,
      isSubmitting: false,
      formErrorMessage: '',
      formData: {
        name: '',
        address: '',
        city: '',
        state: '',
        zip: '',
        country: '',
        lat: '',
        lon: '',
        time_zone: '',
      },
      timezones: [],
      rules: { required: value => !!value || 'Required.' },
    };
  },
  created() {
    this.timezones = momentTz.tz.names();
    if (this.isEditForm && this.site) {
      this.formData.name = this.site.name;
      this.formData.address = this.site.address;
      this.formData.city = this.site.city;
      this.formData.state = this.site.state;
      this.formData.zip = this.site.zip;
      this.formData.country = this.site.country;
      this.formData.lat = this.site.lat;
      this.formData.lon = this.site.lon;
      this.formData.time_zone = this.site.time_zone;
    }
  },
  methods: {
    onSubmit() {
      if (!this.$refs.form.validate()) {
        return;
      }
      if (typeof this.handleSave === 'function') {
        this.isSubmitting = true;
        this.handleSave(this.formData, this.isEditForm ? this.site.uuid : undefined)
          .catch(err => {
            if (err && err.message) {
              this.formErrorMessage = err.message;
            }
          })
          .finally(() => {
            this.isSubmitting = false;
          });
      }
    },
  },
};
</script>
